<template>
    <div class="container">
        <q-banner dense inline-actions
                  class="text-white q-py-sm q-my-sm q-mx-sm text-h6 text-center text-uppercase flex"
                  v-if="banner"
                  style="background: #00800063"
        >
            <div class="row">
                <div class="col-11">
                    <q-icon name="notification_important"></q-icon>
                    Using smart contracts, tokens and crypto has always its risks.
                    DYOR before using our beta.
                </div>
                <div class="col-1 flex items-center justify-end">
                    <q-icon name="close" class="q-pa-xs cursor-pointer" @click="banner=false"></q-icon>
                </div>
            </div>

        </q-banner>
        <div class="row q-py-lg">
            <div class="col-xs-12 col-md-5 col-lg-3 filterSection">
                <button :class=" filter === 'all' ? 'active': ''" @click="filter = 'all'">
                    Current Farms
                </button>
            </div>
            <div class="col-xs-12 col-md-7 col-lg-9 q-py-md q-px-md">
                <div class="q-my-md">
                    <q-table
                            title="Farms"
                            class="transparent custom-table"
                            :data="filteredFarm"
                            :columns="columns"
                            row-key="id"
                    >
                        <template v-slot:body="props">
                            <q-tr :props="props" @click="isMetaMaskConnected ? props.expand = !props.expand : null" class="cursor-pointer">
                                <q-td>
                                    <div class="flex items-center no-wrap">
                                        <q-img :src="props.row.fromImage" style="width:40px;height: 40px"
                                               class="rounded-borders q-mr-sm"></q-img>
                                        <q-img :src="props.row.toImage" style="width:40px;height: 40px"
                                               class="rounded-borders q-mr-sm"></q-img>
                                        <div class="q-ml-xs">
                                            <div class="q-mb-none q-mt-none text-bold">{{props.row.pair}}</div>
                                            <small class="text-grey">{{props.row.farm}}</small>
                                        </div>
                                    </div>
                                </q-td>
                                <q-td class="text-bold">
                                    ${{props.row.volume}}
                                </q-td>
                                <q-td class="text-right">
                                    <div class="text-bold">{{props.row.apr}}%</div>
                                    <div class="text-grey">{{props.row.apr_type}}</div>
                                </q-td>
                            </q-tr>
                            <q-tr v-show="props.expand && isMetaMaskConnected" :props="props">
                                <q-td colspan="5">
                                    <div class="row justify-center">
                                         <div class="col-12 flex justify-between items-center q-pa-sm">
                                             <q-btn class="bg-blue">
                                                {{amountPending(props.row.id - 1)}} BQB
                                             </q-btn>
                                            <q-btn class="gradient-blue" @click="harvest(props.row.id - 1)">
                                                Harvest
                                            </q-btn>
                                        </div>
                                        <div class="cols-12 col-sm-6 q-pa-sm">
                                            <div class="flex justify-between items-center q-mb-lg">
                                                <div></div>
                                                <div class="text-right">
                                                    <div class="box-outlined">Wallet Balance: {{balance(props.row.id - 1)?balance(props.row.id - 1).toFormat(5):0}}</div>
                                                </div>
                                            </div>
                                            <div>
                                                <q-input type="number" v-model="amountStake[props.row.id-1]"
                                                    :key="'input'+rerender"
                                                    standout="bg-white text-white" placeholder="0.0"
                                                    :error-message="errorMessage1(props.row.id - 1)" :error="errorMessage1(props.row.id - 1)!=null" :bottom-slots="false" :hide-bottom-space="true">
                                                    <template v-slot:append>
                                                        <div>
                                                            <q-btn rounded outline color="blue" size="sm" @click="getMaxStake(props.row.id - 1)">
                                                                MAX
                                                            </q-btn>
                                                        </div>
                                                    </template>
                                                </q-input>
                                            </div>
                                            <div>
                                                <button v-if="!isApproved(props.row.id - 1)" class="action" @click="approve(props.row.id - 1)">Approve</button>
                                                <button v-if="isApproved(props.row.id - 1)" class="action" @click="stake(props.row.id - 1)">Stake</button>
                                            </div>
                                        </div>
                                        <div class="cols-12 col-sm-6 q-pa-sm">
                                            <div class="flex justify-between items-center q-mb-lg">
                                                <div></div>
                                                <div class="text-right">
                                                    <div class="box-outlined">Your Staked: {{maxUnstake(props.row.id - 1).toFormat(5)}}</div>
                                                </div>
                                            </div>
                                            <div>
                                                <q-input type="number" v-model="amountUnstake[props.row.id - 1]"
                                                    :key="'input'+rerender"
                                                    standout="bg-white text-white" placeholder="0.0"
                                                    :error-message="errorMessage2(props.row.id - 1)" :error="errorMessage2(props.row.id - 1)!=null" :bottom-slots="false" :hide-bottom-space="true">
                                                    <template v-slot:append>
                                                        <div>
                                                            <q-btn rounded outline color="pink" size="sm" @click="getMaxUnstake(props.row.id - 1)">
                                                                MAX
                                                            </q-btn>
                                                        </div>
                                                    </template>
                                                </q-input>
                                            </div>
                                            <div>
                                                <button v-if="!isApproved(props.row.id - 1)" class="action text-pink" @click="approve(props.row.id - 1)">Approve</button>
                                                <button v-if="isApproved(props.row.id - 1)" class="action text-pink" @click="unstake(props.row.id - 1)">Unstake</button>
                                            </div>
                                        </div>
                                    </div>
                                </q-td>
                            </q-tr>
                        </template>
                    </q-table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import BigNumber from 'bignumber.js';
    export default {
        name: "Farm",
        data() {
            return {
                rerender: 0,
                filter: 'all',
                banner: true,
                text: '',
                columns: [
                    {
                        name: 'pool',
                        field: 'pool',
                        label: 'Pool',
                        align: 'left',
                        sortable: true
                    },
                    {
                        name: 'tvl',
                        field: 'tvl',
                        label: 'TVL',
                        align: 'left',
                        sortable: true
                    },
                    {
                        name: 'apr',
                        field: 'apr',
                        label: 'APR',
                        align: 'right',
                        sortable: true
                    },
                ],
                amountStake: [],
                amountUnstake: [],
            }
        },
        computed: {
            isMetaMaskConnected() {
                return this.$store.state.account!=null
            },
        
            filteredFarm() {
                var rows = [];
                if (this.filter === 'all') {
                    rows = this.rows
                } else {
                    rows = this.rows.filter(item => item.farm_type === this.filter)
                }
                return rows
            },
            rows() {
                return [{
                    id: 1,
                    farm: 'BloqBall Farm For BloqBall LP',
                    pair: 'FTM/BQB',
                    volume: /*BigNumber(this.$store.state.liquidity.amountFTM)
                        .times(this.$store.state.price.rateFTM)
                        .plus(
                            BigNumber(this.$store.state.liquidity.amountBQB)
                                .times(this.$store.state.price.rateBQB)
                        ).shiftedBy(-18).toFormat(2),*/
                        BigNumber(this.$store.state.liquidity.amountFTM)
                                .times(BigNumber(this.$store.state.liquidity.rateFTM2BQB))
                                .plus(this.$store.state.liquidity.amountBQB)
                                .times(this.$store.state.farming.totalSupply[0])
                                .div(this.$store.state.liquidity.totalSupply).shiftedBy(-18).toFormat(2),
                    fromImage: require('@/assets/logo/ftm.png'),
                    toImage: require('@/assets/logo/bqb.png'),
                    img3: require('@/assets/logo/bqb.png'),
                    apr: BigNumber(1).shiftedBy(18).times(365*24*3600*60/100)
                        .div(
                            BigNumber(this.$store.state.liquidity.amountFTM)
                                .times(BigNumber(this.$store.state.liquidity.rateFTM2BQB))
                                .plus(this.$store.state.liquidity.amountBQB)
                                .times(this.$store.state.farming.totalSupply[0])
                                .div(this.$store.state.liquidity.totalSupply)
                        )
                        .toFormat(1),
                    apr_type: 'annualized',
                    farm_type: 'BQB'
                }]
            },
        },
        methods: {
            errorMessage1(index) {
                if(BigNumber(this.amountStake[index]).isGreaterThan(this.balance(index))) {
                    return "INSUFFICIENT ACCOUNT BALANCE11"
                }
                return null
            },
            errorMessage2(index) {
                if(BigNumber(this.amountUnstake[index]).isGreaterThan(this.maxUnstake(index))) {
                    return "INSUFFICIENT ACCOUNT BALANCE22"
                }
                return null
            },
            isApproved(index) {
                if (index == 0)
                    return this.$store.state.approvedPairMaster
            },
            maxUnstake(index) {
                if(!this.$store.state.farming.balance[index])
                    return BigNumber(0)
                return BigNumber(this.$store.state.farming.balance[index])
                    .shiftedBy(-18)
            },  
            amountPending(index) {
                if(BigNumber(this.$store.state.farming.pendingBQB[index]).isNaN())
                    return 0
                return BigNumber(this.$store.state.farming.pendingBQB[index])
                    .shiftedBy(-18)
                    .toFormat(2)
            },
            getMaxStake(index) {
                if (index == 0)
                    this.amountStake[index] = BigNumber(this.$store.state.liquidity.balance)
                    .shiftedBy(-18);
                this.forceUpdate()
            },
            getMaxUnstake(index) {
                this.amountUnstake[index] = BigNumber(this.$store.state.farming.balance[index])
                    .shiftedBy(-18);
                this.forceUpdate()
            },
            balance(index) {
                if (index == 0)
                {
                     if(this.$store.state.account && this.$store.state.liquidity.balance) {
                        return BigNumber(this.$store.state.liquidity.balance)
                            .shiftedBy(-18)
                    }                   
                }
                return 0
            },
            approve(index) {
                if (index == 0)
                    this.$store.dispatch('approvePairMaster')
            },
            stake(index) {
                this.$store.dispatch('deposit',{
                    index:index,
                    amount:this.amountStake[index]
                })
                this.amountStake[index] = 0;
            },
            unstake(index) {
                this.$store.dispatch('withdraw',{
                    index:index,
                    amount:this.amountUnstake[index]
                })
                this.amountUnstake[index] = 0;
            },
            harvest(index) {
                this.$store.dispatch('harvest',index)
            },
            forceUpdate() {
                this.rerender++
            }
        }
    }
</script>

